    <p>Kh&ocirc;ng loi tr kh nng, &atilde; c&oacute; nhng th lc ng sau git d&acirc;y  hp thc h&oacute;a quy tr&igrave;nh tng gi&aacute; sn phm, l&agrave;m vc dy &agrave; tng tng th trng. R&otilde; r&agrave;ng, Chia ph bin s k&eacute;o theo rt nhiu h qu nh ph&acirc;n t&iacute;ch  tr&ecirc;n. Th trng lon lc dn n i gi&aacute;, tr&ecirc;n mi sn phm gi&aacute; tr c&oacute; th tng gp ba gp nm ln. Ngi hng li  &acirc;y ch&iacute;nh l&agrave; nhng nh&agrave; sn xut thit b c&oacute; s dng yu t lu tr nh in thoi, laptop. S chng c&oacute; g&igrave; l&agrave; sai khi nghi ng nhng &ldquo;tay trong c&ocirc;ng ngh&rdquo; ch&iacute;nh l&agrave; nhng ngi git gi&acirc;y cho thng v n&agrave;y. Mt khi Chia th&agrave;nh c&ocirc;ng v&agrave; tr n&ecirc;n ph bin nh Bitcoin, n&oacute; s tr th&agrave;nh mt biu tng cho sc mnh khoa hc c&ocirc;ng ngh cng nh kinh t ca Trung Quc.&nbsp;</p>
