    <p>C&ograve;n mt kh nng na kh&ocirc;ng th b qua, &oacute; l&agrave; c&aacute;c dch v lu tr &aacute;m m&acirc;y s c li rt nhiu sau cn kh&aacute;t  cng. Khi kh&ocirc;ng th trang b cho m&igrave;nh nhng gii ph&aacute;p lu tr cc b na, ngi ta s t&igrave;m n nhng gii ph&aacute;p kh&aacute;c nh lu tr &aacute;m m&acirc;y. C&aacute;c nh&agrave; cung cp dch v s thng m theo hai hng, t&iacute;ch cc v&agrave; ti&ecirc;u cc. T&iacute;ch cc l&agrave; khi h b&aacute;n c nhiu gii ph&aacute;p lu tr hn cho ngi d&ugrave;ng, v&agrave; buc ngi d&ugrave;ng phi ph thuc khi kh&ocirc;ng c&ograve;n  cng. Ti&ecirc;u cc l&agrave; d liu s b &aacute;nh cp  b&aacute;n cho b&ecirc;n th 3. Bng bt c c&aacute;ch n&agrave;o, th&igrave; n&oacute; cng u l&agrave; ng lc th&ocirc;i th&uacute;c c&aacute;c th lc cung cp dch v &aacute;m m&acirc;y nh&uacute;ng tay v&agrave;o k hoch phc tp nhng li v&ocirc; c&ugrave;ng ho&agrave;n ho n&agrave;y.&nbsp;</p>
